<template>

  <div>
    <h3>空间信息编辑</h3>
    <Row>
      <Col>
        <Form :model="formData" :label-width="120">
          <Card style="margin-top: 20px">
            <Divider orientation="left">基础信息</Divider>
            <FormItem class="formItem" label="空间全称">
              <Input v-model="formData.name" placeholder="请填写空间全称"></Input>
            </FormItem>

            <FormItem class="formItem" label="运营团队">
              <Select v-model="formData.operation_team_id">
                <Option v-for="(item,index) in teams" v-bind:key="index" :label="item.name"
                        :value="item.id"></Option>
              </Select>
            </FormItem>

            <FormItem class="formItem" label="所在地区">
              <area-view v-model="areas" style="width: 100%" :ids="defaultAreas"></area-view>
            </FormItem>
          </Card>
          <Card style="margin-top: 20px">
            <Divider orientation="left">介绍资料</Divider>

            <FormItem class="formItem" label="空间简称">
              <Input v-model="formData.short_name" placeholder="请填写空间简称"></Input>
            </FormItem>

            <FormItem class="formItem" label="空间地址">
              <Input v-model="formData.address" placeholder="请填写空间地址"></Input>
            </FormItem>

            <FormItem class="formItem" label="空间简介">
              <Input v-model="formData.short_description" placeholder="请填写空间简介"></Input>
            </FormItem>

            <FormItem class="formItem" label="完整介绍">
              <Input v-model="formData.description" type="textarea" placeholder="请填写完整介绍"></Input>
            </FormItem>
            <FormItem class="formItem" label="位置截图(单张)">
              <ImgSelect v-model="position_picture" :url="formData.position_picture">
              </ImgSelect>
            </FormItem>
            <FormItem class="formItem" label="空间照片(最多6张)">
              <ImgsSelect v-model="space_pictures" :urls="formData.space_pictures" :max="6">
              </ImgsSelect>
            </FormItem>

            <FormItem class="formItem">
              <Button type="success" long @click="submit">提交</Button>
            </FormItem>
          </Card>

        </Form>
        <Spin size="large" fix v-if="load"></Spin>
      </Col>

    </Row>
  </div>
</template>

<script>
import teamApi from '../operating/api'
import api from './api'

import AreaView from '../../components/areaView'
import ImgsSelect from '../../components/imgsSelect'
import ImgSelect from '../../components/imgSelect'

export default {
  name: 'edit',
  components: {
    AreaView, ImgSelect, ImgsSelect
  },
  data () {
    return {
      formData: {},
      areas: [],
      defaultAreas: [],
      teams: [],
      roles: [],
      departments: [],
      load: false,
      position_picture: null,
      space_pictures: null
    }
  },
  mounted () {
    this.loadTeam()
    if (this.$route.query.id != null) {
      this.loadData()
    }
  },
  methods: {
    loadTeam () {
      teamApi.select((res) => {
        console.assert(res)
        this.teams = res.data.data
      })
    },

    loadData () {
      this.load = true
      api.detail(
        { id: this.$route.query.id },
        res => {
          this.load = false
          this.formData = res.data.data
          this.defaultAreas = [this.formData.province.id, this.formData.city.id, this.formData.district.id]
          this.formData.province_id = this.formData.province.id
          this.formData.city_id = this.formData.city.id
          this.formData.district_id = this.formData.district.id
          this.formData.operation_team_id = this.formData.operation_team.id
        }, () => {
          this.load = false
        })
    },
    submit () {
      if (!this.$formUtil.isStrValid(this.formData.name)) {
        this.$Message.error('请输入空间全称')
        return
      }
      if (!this.$formUtil.isStrValid(this.formData.operation_team_id)) {
        this.$Message.error('请选择运营团队')
        return
      }
      if (this.areas.length !== 0) {
        this.formData.province_id = this.areas[0]
        this.formData.city_id = this.areas[1]
        this.formData.district_id = this.areas[2]
      } else if (!this.$formUtil.isStrValid(this.formData.province_id) || !this.$formUtil.isStrValid(this.formData.city_id) || !this.$formUtil.isStrValid(this.formData.district_id)) {
        this.$Message.error('请选择地区')
        return
      }

      if (!this.$formUtil.isStrValid(this.formData.short_name)) {
        this.$Message.error('请输入空间简称')
        return
      }
      if (!this.$formUtil.isStrValid(this.formData.address)) {
        this.$Message.error('请输入空间地址')
        return
      }
      if (!this.$formUtil.isStrValid(this.formData.short_description)) {
        this.$Message.error('请输入空间简介')
        return
      }

      if (!this.$formUtil.isStrValid(this.formData.description)) {
        this.$Message.error('请输入空间完整介绍')
        return
      }
      if (!this.$formUtil.isStrValid(this.formData.address)) {
        this.$Message.error('请输入空间地址')
        return
      }
      let data = this.$formUtil.objToFormData(this.formData)
      if (this.position_picture == null && !this.$formUtil.isStrValid(this.formData.position_picture)) {
        this.$Message.error('请选择位置截图')
        return
      } else if (this.position_picture != null) {
        data.append('position_picture', this.position_picture)
      }
      if (this.space_pictures == null && !this.$formUtil.isStrValid(this.formData.space_pictures)) {
        this.$Message.error('请选择空间照片')
        return
      } else if (this.space_pictures != null) {
        for (let i = 0; i < this.space_pictures.length; i++) {
          data.append('space_pictures[' + i + ']', this.space_pictures[i])
        }
      }

      console.log(data)
      this.load = true
      api.save(data, res => {
        this.load = false
        this.$router.push({ name: 'space' })
      }, () => {
        this.load = false
      })
    }
  }
}
</script>

<style scoped>
  .formItem {
    max-width: 500px;
  }
</style>
