<style scoped>
  .area {
    display: inline-block;
  }
</style>

<template>
  <div class="area">
    <Select @on-change="selectProvince"
            v-model="provinceSelect" style="width: 32%">
      <Option
        v-for="item in province"
        :key="item.id"
        :label="item.name"
        :value="item.id">
      </Option>
    </Select>

    <Select @on-change="selectCity" v-model="citySelect" style="margin-left: 2%;width: 32%">
      <Option
        v-for="item in city"
        :key="item.id"
        :label="item.name"
        :value="item.id">
      </Option>
    </Select>

    <Select @on-change="selectArea" v-model="areaSelect" style="margin-left: 2%;width: 32%">
      <Option
        v-for="item in area"
        :key="item.id"
        :label="item.name"
        :value="item.id">
      </Option>
    </Select>

  </div>
</template>

<script>
import api from '../js/api/area'

export default {
  name: 'areaView',
  props: ['value', 'ids'],
  data () {
    return {
      province: [],
      provinceSelect: '',
      city: [],
      area: [],
      citySelect: '',
      areaSelect: ''
    }
  },
  watch: {
    ids (value) {
      console.log(value)
      console.log('--------')
      this.selectProvince(parseInt(value[0]))
      this.selectCity(parseInt(value[1]))
      this.provinceSelect = parseInt(value[0])
      this.citySelect = parseInt(value[1])
      this.areaSelect = parseInt(value[2])
    }
  },
  mounted () {
    this.loadData()
  },
  methods: {
    loadData () {
      api.index({ level: '1' }, res => {
        this.province = res.data.data
      })
    },
    selectCity (val) {
      this.areaSelect = null
      this.area = []
      if (val !== undefined) {
        api.index({ level: '3', id: val }, res => {
          this.area = res.data.data
        })
      }
    },
    selectProvince (val) {
      this.citySelect = null
      this.city = []
      api.index({ level: '2', id: val }, res => {
        this.city = res.data.data
      })
    },
    selectArea (val) {
      let value = [this.provinceSelect, this.citySelect, val]
      this.$emit('input', value)
      console.log(val)
    }
  }

}
</script>
